import { useRef, useState } from 'react';
import classes from './MealItemForm.module.css';
import Input from '../../UI/Input';

const MealItemForm = ({ id, onAddToCart }) =>{
    const [amountIsValid, setAmountIsValid] = useState(true);
    const amountInputRef = useRef();
    const submitHandler = (e)=>{
        e.preventDefault();

        const enteredAmount = amountInputRef.current.value
        const enteredAmountNumber = +enteredAmount; // This + sign convert string into number
        
        if(enteredAmount.trim().length === 0 || enteredAmountNumber < 1 || enteredAmount > 5){
            setAmountIsValid(false);
            return ;
        };
        onAddToCart(enteredAmountNumber);
    }
    return (
        <form className={classes.form} onSubmit={submitHandler}>
            <Input ref={amountInputRef} label="Amount"  input={{ 
                id: 'amount_' + id,
                type: 'number',
                min: '1',
                max: '5',
                step: '1',
                defaultValue: '1'
            }}/>
            <button>+Add</button>
            {!amountIsValid && <p>Please enter a valid amount from 1 to 5.</p>}
        </form>
    )
};

export default MealItemForm;